.container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    display: flex;
    width: 100%;
    height: 30px;
    background: #fff;
    border: 2px solid black;
    border-radius: 10px;
    font-size: 20px;
    font-family: 'Roboto', sans-serif;
    transition: .1s ease-in-out;
    cursor: pointer;
    padding: 0px 10px;
    box-sizing: border-box;
}

.container:hover {
    background: rgb(199, 199, 199);
}

.active {
    color: #fff;
    background: rgb(0, 0, 0);
}

.timer {
    font-weight: bold;
    font-size: 25px;
}



