.container {
    display: flex;
    justify-content: space-around;
    flex-direction: column;
    align-items: center;
    width: 100%;
    height: 250px;
    background: #fff;
    border-radius: 10px;
    font-family: 'Roboto', sans-serif;
}

.title {
    font-size: 80px;
    font-weight: bold;
}

.description {
    font-size: 45px;
}


.timer {
    padding: 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    background: #000;
}

.timerWrapper{
    width: 100%;
    display: flex;
    justify-content: space-between;
}


