.btn {
    width: 100%;
    height: 70px;
    border: none;
    border-radius: 5px;
    font-size: 30px;
    font-family: 'Roboto', sans-serif;
    transition: .1s ease-in-out;
    border: 1px solid #fff;
}

.btn:hover {
    background: red;
    color: #fff;
    border: 1px solid black;
}


.buttonsWrapper {
    width: 100%;
}
