*{
    margin: 0;
    padding: 0;
}

.App{
    position: fixed;
    width: 100%;
    height: 100%;
}

.container {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
}


.container-wrapper {
    display: grid;
    grid-template-columns: minmax(400px, 800px) minmax(200px, 300px);
    grid-template-rows: 600px;
}


.navbar {
    background: #E3E3E3;
    padding: 30px 15px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.navbar__inputs{
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 40%;
}


.navbar__btns {
    width: 100%;
}

.navbar__btn{
    color: black;
    font-size: 40px;
    width: 50%;
    height: 50px;
    border: 2px solid black;
    background: #E3E3E3;
    transition: .1s ease-in-out;
}

.navbar__btn:hover {
    color: #fff;
    background: black;
}