.container {
    display: flex;
    justify-content: space-around;
    flex-direction: column;
    align-items: center;
    width: 49%;
    height: 250px;
    background: #fff;
    border-radius: 10px;
    font-family: 'Roboto', sans-serif;
}

.title {
    font-size: 80px;
    font-weight: bold;
}

.description {
    font-size: 35px;
}

